ul {
    list-style-type: none;
    margin: auto;
    padding: 0;
    overflow: hidden;
    background-color: white;
}

li {
    float: left;
}

li a {
    display: block;
    color: #333;
    text-align: center;
    padding: 30px 30px;
    text-decoration: none;
    font-family: 'Nanum Gothic', sans-serif;
}

li a:hover:not(.active) {
    color: #fc636b;
}



.header{
  margin:auto;
  display:inline-block;
}

.is-active{
    color: #fc636b;
}



