.aboutme{
  font-family: 'Nanum Gothic', sans-serif;
  color: black;
  font-size: 14px;
}

.container{
  height:500px;
  background-color: white;
  font-family: 'Nanum Gothic', sans-serif;
  font-size: 14px;
}

.myimage{

  height:200px;
  border-radius: 100%;

}

.heartimage{
  height:20px;
  padding:0;
}

.aboutmetable{
  display: -webkit-inline-box;
  margin-top: 1%;
}

.skillstable{
  display: -webkit-inline-box;
  margin-top: 2%;
}

.workex-table{
  display: -webkit-inline-box;
  margin-top: 2%;
}

.acadtable{
  display: -webkit-inline-box;
}

td{
  color:#fc636b;
  font-size: 30px;
  font-weight:800;
  width:250px;
  padding:10px;
}

.td-left{
  color:#fc636b;
  font-size: 30px;
  font-weight:800;
  width:500px;
  padding:10px;
}

.workex-td{
  font-size: 14px;
  font-weight: 300;
  color: #333;
}
.introtext{
  font-family: 'Nanum Gothic', sans-serif;
  font-size: 18px;
}

.othertext{
  text-align: justify;
  width: 700px;
  margin: auto;
}
.icons{
  height:40px;
}

.home-container{
  height:400px;
  background-color: white;
  font-family: 'Nanum Gothic', sans-serif;
  font-size: 14px;
}

.p-light{
  color:#333;
  font-size: 18px;
  text-align: justify;
  font-weight: 200;

}

hr{
  border: 0;
  height: 1px;
  width: 80%;
  background-image: -webkit-linear-gradient(left, #fc636b, #9C27B0, #fc636b) !important;
  background-image: -o-linear-gradient(left, #fc636b, #9C27B0, #fc636b);
}

.icons-sm{
  margin-top: -10px;
  height:35px;
}

.chart-labels-x{
  width:20px;
  font-size: 18px;
  padding:0px !important;
}

.chart-labels-y{
  height:20px;
  font-size: 18px;
  padding:0px !important;
}

.skill-logos{
  height:45px;
}

.logo-td{
  width:80px;
}

a{
  text-decoration: none;
}

a:visited{
  text-decoration: none;
}

a:-webkit-any-link{
  text-decoration: none !important;
}

.company-logos{
  height: 50px;
}

.blk-logo{
  height:25px;
}

.workex-right{
  font-size: 24px;
}

.acad-td{
  font-size: 20px;
  text-align: left;
  vertical-align: text-top;
  width:400px;
}

.acad-p{
  font-size: 14px;
  color:black;
  font-weight: 300;
  text-align: left;
}

.courses-td{
  text-align: right;
}

.courses-p{
  text-align: right;
}

.award-td{
  font-size: 20px;
  text-align: left;
  vertical-align: text-top;
  width:600px;
}

.blog-heading{
  font-size: 16px;
  color:#fc636b;
  font-weight: 400;
}



